<template>
  <section>
    <div class="my-popups">
      <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered modal-lg ui-draggable">
          <div class="modal-content">
            <!-- Header -->
            <div class="modal-header ui-draggable-handle">
              <button type="button" class="close" v-on:click="Close">
                <em aria-hidden="true" class="fa fa-times"></em>
              </button>               
              <h4 class="modal-title" >
                {{ $t('Manage Event') }}
              </h4>
            </div>
          
            <!-- Body -->
            <div class="modal-body">
             <div class="border p-3">
                <Form ref="form">
                    <dynamic-form  lang="en" ref="dynamicform" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                    <template>
                        <div id="divInviteMembers" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2">                            
                            <div class="listing">
                                 <div class="heading-border">
                                    <h5 class="h5">
                                        <span>{{$t("Invite Member(s)")}}</span>                                       
                                </h5>
                            </div>
                            <div class="form-group mt-4">
                                <label class="switch w70">
                                    <input v-model="showInviteMembers" type="checkbox" class="form-check-input custom-control-input dynamic" />
                                    <span class="slider round" style="cursor:default;">
                                        <span class="slider-yes">{{ $t('Yes') }}</span>
                                        <span class="slider-no">{{ $t('No') }}</span>
                                    </span>
                                </label>
                            </div>
                            <div class="table-responsive table-fix-header float-left" style="max-height:350px;"  v-if="showInviteMembers">
                                <table id="tblMembers" class="table table-bordered dt-responsive nowrap mt-0">
                                <thead class="thead-bg">
                                    <tr class="search">
                                    <th style="width:50px !important;" class="text-center">
                                        <label class="form-check form-check-inline">
                                        <div class="custom-control custom-checkbox custom-control-inline">
                                            <!-- <input  id="chkAllManage1" type="checkbox"  class="chkItems custom-control-input"   @change="onChange('chkAllCheckbox')" />  -->
                                            <input id="chkAllManage1" type="checkbox" class="chkItems custom-control-input" :checked="selectedUserIds.length === ListFeedBackData.data.length" @change="onChange('chkAllCheckbox')" />
                                            <label for="chkAllManage1" class="custom-control-label"></label>
                                        </div>
                                        </label>
                                    </th>
                                    <th>
                                        User Name
                                        <input type="text" class="form-control search" @keyup="ListFilteringusername" id="SearhField" placeholder="Search By User Name" />
                                    </th>
                                    <th id="th-EMAIL">
                                        {{$t('Email')}}
                                        <input type="text" class="form-control search" @keyup="ListFilteringEmail" id="SearhFieldemail" placeholder="Search By Email" />
                                    </th>
                                    <th id="th-DESIGNATION">
                                        {{$t('Designation')}}
                                        <input class="form-control search" type="text" @keyup="ListFilteringDesignation" id="SearhFieldDesignation" placeholder="Search By Designation">
                                    </th>
                                    <th id="th-DEPARTMENT">
                                        {{$t('Department')}}
                                        <input type="text" class="form-control search" @keyup="ListFilteringDepartment" id="SearhFielddepartment" placeholder="Search By Department" />
                                    </th>
                                    </tr>
                                </thead>

                                <tbody v-if="searchfound==true">
                                    <tr>
                                    <td colspan="5" class="text-center text-danger no_records no-record">
                                        No record(s) found
                                    </td>
                                    </tr>
                                </tbody>
                                
                                <tbody id="tblUserList" v-else>
                                    <tr v-for="(item, index) in searchColumnfilter.length != 0 ? searchColumnfilter : ListFeedBackData.data" :key="item.user_id">
                                    <td style="width:50px !important;" class="text-center">
                                        <label class="form-check form-check-inline">
                                        <div class="custom-control custom-checkbox custom-control-inline">
                                            <input 
                                            :id="'chkAll'+index" 
                                            type="checkbox" 
                                            class="chkinneritem custom-control-input" 
                                            :checked="selectedUserIds.find(x=> x == item.user_id)" 
                                            :value="item.user_id" 
                                            @change="onChange('innerChkBoxes')" 
                                            />
                                            <label :for="'chkAll'+index" class="custom-control-label"></label>
                                        </div>
                                        </label>
                                    </td>
                                    <td>
                                        <span>{{ item.username }}</span>
                                    </td>
                                    <td>
                                        <span>{{ item.email_id }}</span>
                                    </td>
                                    <td>
                                        <span>{{ item.designation_name }}</span>
                                    </td>
                                    <td>
                                        <span>{{ item.department_name }}</span>
                                    </td>
                                    </tr>
                                </tbody>
                                </table>
                            </div>
                            </div>
                        </div>
                    </template>

                     <template>
                        <div id="divInviteClient" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2">                            
                            <div class="listing mt-4">
                                 <div class="heading-border">
                                    <h5 class="h5">
                                        <span>{{$t("Invite Client(s)")}}</span>                                       
                                </h5>
                            </div>
                            <div class="form-group mt-4 col-sm-6 col-md-5 col-lg-5 float-left pr-1 pl-0 px-small-0">
                                <label class="switch w70">
                                    <input v-model="showInviteClient"  v-on:change="GetProjectList" type="checkbox" class="form-check-input custom-control-input dynamic" />
                                    <span class="slider round" style="cursor:default;">
                                        <span class="slider-yes">{{ $t('Yes') }}</span>
                                        <span class="slider-no">{{ $t('No') }}</span>
                                    </span>
                                </label>
                            </div>
                             <div class="col-sm-6 col-md-5 col-lg-5 float-left pr-1 pl-0 px-small-0 mt-4" v-if="showInviteClient">
                                <div class="form-group mb-1">
                                    <select class="form-control" v-model="ProjectId" @change="onProjectChange">
                                        <option value="">{{ $t("Select Project") }}</option>
                                        <option v-for="(item, itemIndex) in ProjectData" :key="itemIndex"
                                            v-bind:value="item.PROJECT_ID">{{ item.PROJECT_NAME }}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="table-responsive table-fix-header float-left" style="max-height:350px;" v-if="isShowProject">
                                <table id="tblMembers" class="table table-bordered dt-responsive nowrap mt-0">
                                  <thead class="thead-bg">
                                    <tr class="search">
                                    <th style="width:50px !important;" class="text-center">
                                        <label class="form-check form-check-inline">
                                        <div class="custom-control custom-checkbox custom-control-inline">
                                            <!-- <input  id="chkAllManage1" type="checkbox"  class="chkItems custom-control-input"   @change="onChange('chkAllCheckbox')" />  -->
                                            <input id="chkAllManage2" type="checkbox" class="chkItems custom-control-input" :checked="selectedUserId.length === ClientData.length" @change="onChangeCheck('chkAllbox')" />
                                            <label for="chkAllManage2" class="custom-control-label"></label>
                                        </div>
                                        </label>
                                    </th>
                                    <th id="th-USERNAME">
                                        User Name
                                        <input type="text" class="form-control search" @keyup="FilteringUserName" id="SearhFieldUserName" placeholder="Search By User Name" />
                                    </th>
                                    <th id="th-EMAILID">
                                        {{$t('Email')}}
                                        <input type="text" class="form-control search" @keyup="FilteringEmail" id="SearhFieldEmail" placeholder="Search By Email" />
                                    </th>
                                    <th id="th-STATUSNAME">
                                        {{$t('STATUS_NAME')}}
                                        <input class="form-control search" type="text" @keyup="FilteringStatusName" id="SearhFieldStatusName" placeholder="Search By Status Name">
                                    </th>                                  
                                    </tr>
                                </thead> 

                                 <tbody v-if="searchfound==true">
                                    <tr>
                                    <td colspan="5" class="text-center text-danger no_records no-record">
                                        No record(s) found
                                    </td>
                                    </tr>
                                </tbody> 
                                
                                 <tbody id="tblUserList" v-else>
                                    <tr v-for="(item, index) in searchfilter.length != 0 ? searchfilter : ClientData" :key="item.USER_ID">                                       
                                    <td style="width:50px !important;" class="text-center">
                                        <label class="form-check form-check-inline">
                                        <div class="custom-control custom-checkbox custom-control-inline">
                                            <input 
                                            :id="'CheckAll'+index" 
                                            type="checkbox" 
                                            class="chkinneritem custom-control-input" 
                                            :checked="selectedUserId.find(x=> x == item.USER_ID)" 
                                            :value="item.USER_ID" 
                                            @change="onChangeCheck('innerChkBoxe')" 
                                            />
                                            <label :for="'CheckAll'+index" class="custom-control-label"></label>
                                        </div>
                                        </label>
                                    </td>
                                    <td>
                                        <span>{{ item.USER_NAME }}</span>
                                    </td>
                                    <td>
                                        <span>{{ item.EMAIL_ID }}</span>
                                    </td>
                                    <td>
                                        <span>{{ item.STATUS_NAME }}</span>
                                    </td>                                    
                                    </tr>
                                </tbody> 
                                </table>
                            </div>
                            </div>
                        </div>
                    </template>                                     
                       <div class="form-group mt-2" v-if="EventId">
                            <div class="p-3">
                                <label class="mt-4"><strong>Do you want to reschedule the entire series of this event?:</strong></label><br>
                                <input type="radio" id="scheduleYes" value="yes" name="reschedule" v-model="isReschedule">
                                <label class="mr-2" for="scheduleYes">Yes</label>
                                <input type="radio" id="scheduleNo" value="no" name="reschedule" v-model="isReschedule">
                                <label  class="mr-2" for="scheduleNo">No</label>
                            </div>
                        </div>
                    
                        <div class="row flex-row-reverse mt-2 pt-2">
                            <div class="col-lg-6 text-right">
                                <button type="submit" @click="onSubmit" class="btn btn-success mr-2"  title="Save">
                                    <i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                                </button> 

                                <a  href="javascript:;" class="btn btn-danger ml-2" v-on:click="Close" title="">
                                    <i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                                </a>
                            </div>
                        </div>
                </Form> 
            </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>


<script>
import { event } from "jquery";
import DataService from "../../services/DataService";

export default {
  props: {
    eventData: {
      type: Array,
      required:false
    },
  },
  data() {
    return {        
        ListFeedBackData: [],
        selectedUserIds: [],
        selectedUserId: [],
        searchusername: '',
        searchemail: '',
        searchDesignation: '',
        searchDepartment: '',
        searchfound: false,
        searchColumnfilter: [],
        searchfilter: [],
        SelectedFeedBackIds: [],
        SelectedClientId: [],
        sliderValue: null,        
        showInviteMembers: false,
        showInviteClient: false,
        isShowProject:false,
        isReschedule:false,
        ProjectData:[],
        ClientData:[],
        EventData:[],
        RecurringData:[],
        ReminderData:[],
        LocationData:[],
        ProjectId:'',
        EventId:'',
        PriorityData:[],
        SourcesData:[],
        FindalLocationArray:[],
        SubCatData:[],
        FormSchema: [
            {
            layoutType: "double",
            group_name: this.$t('General Information'),
            Data: [
                            {
                                astype: "TextField",
                                label: this.$t('Subject'),
                                name: "Subject",
                                value: "",
                                placeholder: "Subject",
                                validationRules: {
                                    "required": true
                                },
                                disabled: false,
                            },
                            {
                                astype: "TextField",
                                label: this.$t('Agenda'),
                                name: "Agenda",
                                value: "",
                                placeholder: "Agenda",
                                validationRules: {
                                    "required": false
                                },
                            },
                            {
                                astype: "TextField",
                                label: this.$t('Speaker'),
                                name: "Speaker",
                                value: "",
                                placeholder: "Speaker",
                                validationRules: {
                                    "required": false
                                },
                            },
                            {
                                astype: "CheckBoxField",
                                label: this.$t('Is All Day'),
                                name: "IsAllDay",
                                value: "",
                                config: {
                                    options: [
                                        { name: '', value: "true" }
                                    ],
                                    onChange: this.onChangeWireCenter
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "DateField",
                                label: this.$t("Start Date"),
                                name: "StartDate",
                                value: new Date(),
                                placeholder: this.$t("StartDate"),
                                validationRules: {
                                    required: true,
                                },
                                config: {
                                    mode: "date",
                                    format: "M/D/YYYY"
                                },
                            },
                            {
                                astype: "DateField",
                                label: this.$t("End Date"),
                                name: "EndDate",
                                value: new Date(),
                                placeholder: this.$t("EndDate"),
                                validationRules: {
                                    required: true,
                                },
                                config: {
                                    mode: "date",
                                    format: "M/D/YYYY"
                                },
                            },
                            {
                                astype: "DateField",
                                label: this.$t('Start Time'),
                                name: "Start Time",
                                value: "",
                                placeholder: "Please Select",
                                visibility: true,
                                config: {
                                    mode: "time",
                                    is24hr: true,
                                // format: "hh:mm",
                                    
                                }
                            },
                            {
                                astype: "DateField",
                                label: this.$t('End Time'),
                                name: "End Time",
                                value: "",
                                placeholder: "Please Select",
                                visibility: true,
                                config: {
                                    mode: "time",
                                    is24hr: true,
                                // format: "hh:mm",
                                    
                                }
                            },
                            {
                                astype: "TextField",
                                label: this.$t('Event Url'),
                                name: "EventUrl",
                                value: "",
                                placeholder: "Event Url",
                                validationRules: {
                                    "required": true
                                },
                                disabled: false,
                            },
                            {
                                astype: "TextField",
                                label: this.$t('Contact Person'),
                                name: "ContactPerson",
                                value: "",
                                placeholder: "Contact Person",
                                validationRules: {
                                    "required": false
                                },
                            },                        
                            {
                                astype: "TextField",
                                label: this.$t('Phone Number'),
                                name: "PhoneNumber",
                                value: "",
                                placeholder: "Phone Number",
                                validationRules: {
                                    "required": false
                                },
                                disabled: false,
                            },
                            {
                                astype: "TextField",
                                label: this.$t('Email'),
                                name: "Email",
                                value: "",
                                placeholder: "Email",
                                validationRules: {
                                    "required": false
                                },
                            },
                            {
                                astype: "TextField",
                                label: this.$t('Event Website'),
                                name: "EventWebsite",
                                value: "",
                                placeholder: "Event Website",
                                validationRules: {
                                    "required": false
                                },
                            },
                            
            
            ]
            },
            {
            layoutType: "double",
            group_name: this.$t('Venue'),
            Data: [ 
                        {
                            astype: "SelectField",
                            label: this.$t('Venue'),
                            name: "Venue",
                            value: "",
                            placeholder: "Venue",
                            config: {
                                options: [],
                                //onChange: this.GetSubCategoryDDL
                            },
                            validationRules: {
                                "required": true
                            },
                        },                       
                        {
                            astype: "SelectField",
                            label: this.$t('Recurring'),
                            name: "Recurring",
                            value: "",
                            placeholder: "Recurring",
                            config: {
                                options: [],
                                //onChange: this.GetSubCategoryDDL
                            },
                            validationRules: {
                                "required": false
                            },
                        }, 
                        {
                            astype: "SelectField",
                            label: this.$t('Event Reminder'),
                            name: "EventReminder",
                            value: "",
                            placeholder: "Event Reminder",
                            config: {
                                options: [],
                                //onChange: this.GetSubCategoryDDL
                            },
                            validationRules: {
                                "required": true
                            },
                        }, 
                        {
                            astype: "SelectField",
                            label: this.$t('Priority'),
                            name: "Priority",
                            value: "",
                            placeholder: "Priority",
                            config: {
                                options: [],
                                //onChange: this.GetSubCategoryDDL
                            },
                            validationRules: {
                                "required": true
                            },
                        }, 
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: 1001,
                            placeholder: "Status",
                            config: {
                                options: [],
                                //onChange: this.GetSubCategoryDDL
                            },
                            validationRules: {
                                "required": true
                            },
                        }, 
                        {
                            astype: "RadioField",
                            label: "Do you want to share this with Google?",
                            name: "isShareWithGoogle",
                            value: 0,
                            placeholder: "",
                            disabled: false,
                            chktxtIndex: 0,
                            visibility: true,
                            config: {
                                options: [
                                    { value: 0, name: "No" },
                                    { value: 1, name: "Yes" }
                                ],
                                //onChange: vm.OnChangeEventsforField // Optional
                            },
                            validationRules: {
                                "required": false
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Google Calendar'),
                            name: "GoogleCalendar",
                            value: "",
                            placeholder: "Google Calendar",
                            config: {
                                options: [],
                                //onChange: this.GetSubCategoryDDL
                            },
                            validationRules: {
                                "required": false
                            },
                        }, 
                         {
                            astype: "RadioField",
                            label: "",
                            name: "Accessibility",
                            value: 1,
                            placeholder: "",
                            visibility: false,
                            config: {
                                options: [{
                                    name: 'Private',
                                    value: 0
                                }, {
                                    name: 'Public',
                                    value: 1
                                }],
                                showAddIcon: false,
                            },
                        }                    
            
                ]
            },
            {
            layoutType: "single",
            group_name: this.$t('Description'),
            Data: [ 
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            config: {
                                rows: 3
                            },
                            placeholder: this.$t(''),
                            validationRules: {
                                "required": true,
                                max: 2000,
                                // "regex": /^[a-zA-Z0-9\s]*$/
                            },
                        }                                       
            
                ]
            },
        //  {
        //   layoutType: "single",
        //   group_name: this.$t('Invite Member(s)'),
        //   Data: [ 
        //            {
        //                 astype: "toggleField",
        //                 label: this.$t('Invite Member(s)'),
        //                 name: "InviteMember",
        //                 value: "",
        //                 placeholder: "",
        //                 config: {},
        //                 visibility: true
        //             },                                      
           
        //     ]
        // },
        //  {
        //   layoutType: "Double",
        //   group_name: this.$t('Invite Client(s)'),
        //   Data: [ 
        //            {
        //                 astype: "toggleField",
        //                 label: this.$t('Invite Client(s)'),
        //                 name: "InviteClient",
        //                 value: "",
        //                 placeholder: "",
        //                 config: {},
        //                 visibility: true
        //             },
        //             {
        //                 astype: "SelectField",
        //                 label: this.$t('Project'),
        //                 name: "Project",
        //                 value: "",
        //                 placeholder: "Proejct",
        //                 config: {
        //                     options: [],
        //                     //onChange: this.GetSubCategoryDDL
        //                 },
        //                 validationRules: {
        //                     "required": true
        //                 },
        //             },                                       
           
        //     ]
        // },
        ],
    //   buttons: [
    //     { type: "submit", class: "btn btn-success", text: "<i class='fa fa-save pr-2'></i> Save" },
    //     { type: "button", class: "btn btn-danger", text: "<i class='fa fa-close pr-2'></i> Cancel", onClick: this.Close }
    //   ]
    };
  },
  async  created() {
    if (this.eventData && typeof(this.eventData) !='undefined') {        
        this.EventId=this.eventData.id;               
    }
    if(this.GetUserInfo.UserType!='CL'){
        (this.FormSchema)[1].Data[7].visibility=true;
    }
    await this.GetLocationByuserId();
    this.getuserlist();
    await this.GetEventByEventId();
    await this.GetStatusList();
    await this.GetPriority();
    await this.EventRecurring();
    await this.EventReminder();
    await this.GetCalSources();  
  },
  methods: {
        Close: function () {            
            this.$parent.Close();    
        },
        getuserlist() {
            var vm = this;
            vm.ListFeedBackData = [];
            var params = `FeedBackUserid=${vm.GetUserInfo.ID}`;

            DataService.GetUserfeedback(params).then(function (response) {
                var listdata = JSON.parse(response.data[0]);

                if (listdata != null && listdata != undefined) {
                    vm.ListFeedBackData = listdata;                 
                    vm.noRecords = vm.ListFeedBackData.length === 0;
                    vm.TotalRecords = vm.ListFeedBackData.length;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = parseInt(vm.PageNumber);
                }
            });
        },

        async GetEventByEventId() {
                var vm = this;
                vm.isLoading = true;
                if(vm.EventId>0) {               
                    var url = `eventId=${vm.EventId}`;
                    await DataService.GetEventByEventId(url).then((response) => {                    
                    if (response.data != null) {
                        vm.EventData = [];
                        vm.EventData = response.data;    
                        
                        (vm.FormSchema)[0].Data[0].value = vm.EventData.Event.EVENT_SUBJECT,
                        (vm.FormSchema)[0].Data[1].value = vm.EventData.Event.AGENDA,
                        (vm.FormSchema)[0].Data[2].value = vm.EventData.Event.SPEAKER,
                        (vm.FormSchema)[0].Data[3].value = vm.EventData.Event.EVENT_IS_ALLDAY==1 ? "true" : '', 
                        (vm.FormSchema)[0].Data[4].value = vm.$options.filters.formatDate(vm.EventData.Event.EVENT_START_TIME),
                        (vm.FormSchema)[0].Data[5].value = vm.$options.filters.formatDate(vm.EventData.Event.EVENT_END_TIME),
                        (vm.FormSchema)[0].Data[6].value = vm.$options.filters.formatTime(vm.EventData.Event.EVENT_START_TIME), 
                        (vm.FormSchema)[0].Data[7].value = vm.$options.filters.formatTime(vm.EventData.Event.EVENT_END_TIME),
                        (vm.FormSchema)[0].Data[8].value = vm.EventData.Event.EVENT_URL,
                        (vm.FormSchema)[0].Data[9].value = vm.EventData.Event.CONTACT_PERSON,
                        (vm.FormSchema)[0].Data[10].value = vm.EventData.Event.PHONE_NUMBER,
                        (vm.FormSchema)[0].Data[11].value = vm.EventData.Event.EMAIL_ID,
                        (vm.FormSchema)[0].Data[12].value = vm.EventData.Event.EVENT_WEBSITE, 
                        
                        (vm.FormSchema)[1].Data[0].value = vm.EventData.Event.EVENT_LOCATION_ID,
                        (vm.FormSchema)[1].Data[1].value = vm.EventData.Event.EVENT_RECURRING_ID,
                        (vm.FormSchema)[1].Data[2].value = vm.EventData.Event.EVENT_REMINDER_ID,
                        (vm.FormSchema)[1].Data[3].value = vm.EventData.Event.EVENT_PRIORITY_ID,
                        (vm.FormSchema)[1].Data[4].value = vm.EventData.Event.STATUS_ID,
                        //     isShareWithGoogle: (vm.FormSchema)[1].Data[5].value = vm.EventData.Event.EVENT_SUBJECT,
                        (vm.FormSchema)[1].Data[6].value = vm.EventData.Event.CAL_SOURCE_ID,
                        (vm.FormSchema)[2].Data[0].value = vm.EventData.Event.EVENT_DESC,
