        onSubmit(formfields) {            
            var vm = this;
            vm.$refs.form.validate().then(async valid => {
                if (valid) {
                    vm.isLoading = true;
                   
                    let obj = {
                        subject: (vm.FormSchema)[0].Data[0].value || '',
                        agenda: (vm.FormSchema)[0].Data[1].value || '',
                        speaker: (vm.FormSchema)[0].Data[2].value || '',
                        is_all_day: (vm.FormSchema)[0].Data[3].value=='true' ? 1 : 0,
                        start_date: vm.$options.filters.formatDate((vm.FormSchema)[0].Data[4].value) || '',
                        end_date: vm.$options.filters.formatDate((vm.FormSchema)[0].Data[5].value) || '',
                        start_time: vm.$options.filters.formatTime((vm.FormSchema)[0].Data[6].value) || '', 
                        end_time: vm.$options.filters.formatTime((vm.FormSchema)[0].Data[7].value) || '',
                        event_url: (vm.FormSchema)[0].Data[8].value || '',
                        contact_person: (vm.FormSchema)[0].Data[9].value || '',
                        phone_number: (vm.FormSchema)[0].Data[10].value || '',
                        email: (vm.FormSchema)[0].Data[11].value || '',
                        event_website: (vm.FormSchema)[0].Data[12].value || '',

                        venue: (vm.FormSchema)[1].Data[0].value || '',
                        recurring: (vm.FormSchema)[1].Data[1].value || '',
                        event_reminder: (vm.FormSchema)[1].Data[2].value || '',
                        priority: (vm.FormSchema)[1].Data[3].value || '',
                        status: (vm.FormSchema)[1].Data[4].value || '',
                        isShareWithGoogle: ((vm.FormSchema)[1].Data[5].value ==0)? false: "true",
                        cal_source_id: (vm.FormSchema)[1].Data[6].value || '',
                        accessibility: (vm.FormSchema)[1].Data[7].value || '',

                        description: (vm.FormSchema)[2].Data[0].value || '',
                        projectId: vm.ProjectId ||'',

                        eventId:vm.EventId||0,

                        Invite_Member_Ids:  vm.selectedUserIds,
                        Invite_Client_ids:  vm.selectedUserId,
                        is_edit_future_record: vm.isReschedule=='yes' ? 1: 0
                    };
                    var Jsondata = JSON.stringify(obj);

                    DataService.AddupdateEvent(Jsondata).then(response => {
                       vm.isLoading = false;
                        if (response.status === 200) {
                            const result = response.data.result[0];         

                            if (result.Status === 2) {
                                vm.ShowAlert(result.StatusMessage, "success", true, vm.$t("Alert"));
                            } else {
                                vm.ShowAlert(result.StatusMessage, "failure", true, vm.$t("Alert"));
                            }
                        }
                    });
                }
            });
        }

