<template>
  <section>
    <loader :is-visible="isLoading"></loader>

    <div class="mt-2">
      <!-- FullCalendar Component -->
      <FullCalendar ref="calendar" :options="calendarOptions" />

      <!-- Optional: task details (hidden when editing) -->
      <div v-if="selectedTask && !isAddCloseEvent" class="task-details">
        <h4>{{ selectedTask.title }}</h4>
        <p><strong>Start:</strong> {{ selectedTask.startStr }}</p>
        <p><strong>End:</strong> {{ selectedTask.endStr }}</p>
        <!-- <p><strong>Description:</strong> {{ selectedTask.extendedProps.description }}</p> -->
      </div>
    </div>

    <!-- Add/Edit Event Modal -->
    <AddEvent v-if="isAddCloseEvent" :eventData="selectedTask" @Close="Close" ></AddEvent>
  </section>
</template>

<script>
import FullCalendar from "@fullcalendar/vue";
import dayGridPlugin from "@fullcalendar/daygrid";
import DataService from "../../services/DataService";
import AddEvent from "../../../src/components/Modals/AddEvent.vue";

export default {
  components: { FullCalendar, AddEvent },
  data() {
    return {
      ProjectId: 0,
      isLoading: false,
      isAddCloseEvent: false,
      ListingData: [],
      PageSize: 100000,
      PageNumber: 1,
      SortBy: "",
      SortExp: "",
      tasks: [],
      selectedTask: null,
      currentPopover: null,
      calendarOptions: {
        plugins: [dayGridPlugin],
        initialView: "dayGridMonth",
        events: [],
        eventClick: this.onEventClick,
        eventMouseLeave: this.onEventMouseLeave,

        //  Toolbar setup
          headerToolbar: {
          left: "prev,next today addEventButton",
          center: "title",
          right: "dayGridMonth,dayGridWeek,dayGridDay",
        },

        //  Custom button
        customButtons: {
          addEventButton: {
            text: "Add Event",
            click: () => {
              this.addEventPopup();
            },
          },
        },
      },
    };
  },
  mounted() {
    this.LoadProjectTaskCalendar();
  },
  // created (){
  //   debugger;
  //   this.LoadProjectTaskCalendar();
  // },
  methods: {
    async LoadProjectTaskCalendar() {
      this.isLoading = true;
      const toDate = this.$options.filters.formatDateYear(new Date()); // today
      const fromDate = this.$options.filters.formatDateYear(
        new Date(new Date().setDate(new Date().getDate() - 60))
      );

      const params = `userId=${this.GetUserInfo.ID}&fromDate=${fromDate}&toDate=${toDate}&locationId=0&requestedEventType=requestedEventType`;

      try {
        const response = await DataService.LoadProjectTaskCalendar(params);

        if (response.data?.length > 0) {
          this.ListingData = response.data;

          this.tasks = this.ListingData.map((itm) => ({
            id: itm.EVENTID,
            name: itm.EVENTINFO,
            dateFrom: itm.EVENTSTART,
            dateTo: itm.EVENTEND,
            color: this.getStableColor(itm.EVENTID),
            description: `${itm.EVENTTYPE} | ${itm.INVITATION_STATUS}`,
            isAllDay: itm.IS_ALLDAY === 1,
          }));

          this.updateCalendarEvents();
        }
      } catch (err) {
        console.error("Failed to fetch events", err);
      } finally {
        this.isLoading = false;
      }
    },

    //  Consistent color per event (based on EVENTID)
    getStableColor(id) {
      const hash = id.toString().split("").reduce((acc, char) => acc + char.charCodeAt(0), 0);
      return "#" + (hash & 0xffffff).toString(16).padStart(6, "0");
    },
    Close (){
      var vm = this;
      vm.isAddCloseEvent = false;
    },
    onEventMouseLeave(info) {
      if (this.currentPopover) {
        $(this.currentPopover).popover("hide");
        this.currentPopover = null;
      }
    },

    updateCalendarEvents() {
      const calendarApi = this.$refs.calendar.getApi();
      calendarApi.removeAllEvents();

      this.tasks.forEach((task) => {
        calendarApi.addEvent({
          id: task.id,
          title: task.name,
          start: task.dateFrom,
          end: task.isAllDay ? this.addOneDay(task.dateTo) : task.dateTo,
          color: task.color,
          allDay: task.isAllDay,
          extendedProps: {
            description: task.description,
          },
        });
      });
    },

    //  Add new event
    addEventPopup() {
      var vm = this;
      vm.selectedTask = null; // reset for new
      vm.isAddCloseEvent = true;
    },

    //  Edit existing event
    onEventClick(info) {
      var vm = this;
      this.selectedTask = {
        id: info.event.id,
        title: info.event.title,
        start: info.event.start,
        end: info.event.end,
        allDay: info.event.allDay,
        description: info.event.extendedProps.description,
      };
      vm.isAddCloseEvent = true;
    },

    addOneDay(dateStr) {
      const date = new Date(dateStr);
      date.setDate(date.getDate() + 1);
      return date.toISOString().split("T")[0];
    },

    //  Handle save from AddEvent.vue
    // handleEventSaved() {
    //   this.isAddCloseEvent = false;
    //   this.selectedTask = null;
    //   this.LoadProjectTaskCalendar();
    // },
  },
};
</script>

<style>
/* @import "~@fullcalendar/common/main.css";
@import "~@fullcalendar/daygrid/main.css"; */
.task-details {
  margin-top: 20px;
  padding: 15px;
  border: 1px solid #ccc;
  background: #f9f9f9;
  max-width: 300px;
}
.fc-theme-standard th {
  background: #2c3e50;
  line-height: 40px;
}
.fc-theme-standard th a {
  color: #fff;
}
</style>
